<template>
  <div>
    <sub-header titleText="上传视频" />
    <div class="container">
      <Upload
        type="drag"
        action="//jsonplaceholder.typicode.com/posts/">
        <div style="padding: 20px 0">
          <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
          <p>Click or drag files here to upload</p>
        </div>
      </Upload>
      <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
          <FormItem label="标题" prop="name">
              <Input v-model="formValidate.name" placeholder="Enter your name" />
          </FormItem>
          <FormItem label="视频简介" prop="desc">
              <Input v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="Enter something..." />
          </FormItem>
          <FormItem label="视频封面" prop="poster">
            <ImgUpload />
          </FormItem>
          <!--分类-->
          <FormItem label="视频分类" prop="type">
            <Select v-model="model1" style="width:200px">
              <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem>
              <Button type="primary" @click="handleSubmit('formValidate')">Submit</Button>
              <Button @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button>
          </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import SubHeader from '../components/SubHeader'
import {Upload, Icon, Form, FormItem, Button, Input, Select} from 'iview'
import ImgUpload from '../components/ImgUpload'

export default{
  components: {
    subHeader: SubHeader,
    ImgUpload,
    Upload,
    Icon,
    Form,
    FormItem,
    Button,
    Input,
    Select
  },
  data () {
    return {
      cityList: [
        {
          value: 'New York',
          label: 'New York'
        },
        {
          value: 'London',
          label: 'London'
        },
        {
          value: 'Sydney',
          label: 'Sydney'
        },
        {
          value: 'Ottawa',
          label: 'Ottawa'
        },
        {
          value: 'Paris',
          label: 'Paris'
        },
        {
          value: 'Canberra',
          label: 'Canberra'
        }
      ],
      model1: '',
      formValidate: {
        name: '',
        desc: ''
      },
      ruleValidate: {
        name: [
          {required: true, message: 'The name cannot be empty', trigger: 'blur'}
        ],
        desc: [
          {required: true, message: 'Please enter a personal introduction', trigger: 'blur'},
          {type: 'string', min: 20, message: 'Introduce no less than 20 words', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success('Success!')
        } else {
          this.$Message.error('Fail!')
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    }
  }
}
</script>
<style>
  .container {
    width: 660px;
    margin: 0 auto;
  }
</style>



