<template>
  <div>
    <router-link to="/" class="logo">
      <img src="../assets/logo.png" />
    </router-link>
    <div class="channels">
      <div class="item" v-for="channel in channelData" v-bind:key="channel.key">{{channel.name}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    channels: {
      type: Object
    }
  },
  // data () {
  //   return {
  //     channels: [
  //       {name: '推荐', key: 1},
  //       {name: '音乐', key: 2},
  //       {name: '影视', key: 3},
  //       {name: '农人', key: 4},
  //       {name: '游戏', key: 5},
  //       {name: '儿童', key: 6},
  //       {name: '美食', key: 7},
  //       {name: '生活', key: 8},
  //       {name: '体育', key: 9},
  //       {name: '懂车帝', key: 10},
  //       {name: '文化', key: 11},
  //       {name: '时尚', key: 12},
  //       {name: '科技', key: 13},
  //       {name: '广场舞', key: 14}
  //     ]
  //   }
  // },
  computed: {
    channelData () {
      console.log(Object.keys(this.channels))
      let tmpArr = Object.keys(this.channels).map(key => ({key, name: this.channels[key]}))
      return [{name: '推荐', key: 0}].concat(tmpArr)
    }
  }
}
</script>

<style scoped>
  .logo {
    display: block;
    width: 110px;
    height: 28px;
    margin-bottom: 16px;
  }
  .channels > .item {
    display: block;
    width: 110px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #444;
    border-radius: 4px;
    margin-bottom: 2px;
    transition-property: color,background-color;
  }
  .channels > .item:hover {
    background-color: #F23D58;
    color: #fff;
    cursor: pointer;
  }
</style>



